.field-programs {
  border: none;
  margin: 0;
  padding: 0;
}

.field-programs__list {
  margin: 0;
  list-style-type: none;
  padding-left: 0;
}

.field-programs__label {
  font-family: "Oswald", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
  color: #222222;
}

.field-programs__item {
  margin-bottom: 13px;
}
