.example__block {
padding: 25px 20px 40px 20px;
  background-color: #eaeaea;
}

.example__block-title {
  display: flex;
  margin-bottom: 40px;
}

.example__title {
  font-family: "Oswald", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: 40px;
  text-align: center;
  color: #000000;
  margin: 0;
}

.example__block-text {
  margin-bottom: 20px;
}

.example__text {
  font-family: "Lato", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: #444444;
  margin: 0;
}

.example__data-information {
  display: flex;
  justify-content: center;
  gap: 32px;
  margin: 0 auto;
  padding-bottom: 20px;
}

.example__targets {
  font-family: "Oswald", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 24px;
  text-align: center;
  color: #000000;
  margin: 0;
}

.example__targets-text {
  font-family: "Lato", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 12px;
  color: #444444;
  margin: 0;
  text-align: center;
  align-items: center;
}

.example__total {
  font-family: "Oswald", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  text-align: center;
  color: #000000;
  margin: 0;
}
